import React from "react"
import PropTypes from "prop-types"

/*
 * gatsby-plugin-transition-link can mess with hot-reloading
 * see https://github.com/TylerBarnes/gatsby-plugin-transition-link/issues/131
 * put DISABLE_TRANSITION_LINK=true into .env.development to disable it
 */
const Link = process.env.DISABLE_TRANSITION_LINK
  ? require("gatsby").Link
  : require("gatsby-plugin-transition-link/AniLink").default

const LinkTo = ({ children, ...props }) => {
  const { href, style } = props

  return (
    <Link style={style} paintDrip to={href} duration={0.55} hex="#07141d">
      {children}
    </Link>
  )
}

LinkTo.propTypes = {
  children: PropTypes.node.isRequired,
  href: PropTypes.string.isRequired,
  style: PropTypes.oneOfType([
    PropTypes.object,
    PropTypes.string,
    PropTypes.array,
  ]),
}

LinkTo.defaultProps = {
  style: {},
}

export default LinkTo
